<template>
  <div style="display: flex; flex-direction: column;">
    <div style="display: flex; flex-direction: row; justify-content: center; align-items: center; margin: 40px">
      <div>
        <el-input
            v-model="search"
            @keyup.enter.native="doSearch()"
            placeholder="Suche nach"
        >
          <el-button slot="append" icon="el-icon-search" @click="doSearch(); getData()"></el-button>
        </el-input>
      </div>
    </div>
    <div class="table">
      <el-table
          :data="tableData"
          onclick="console.log()"
      >
        <el-table-column v-for="column in columns"
                         :key="column.label"
                         :prop="column.prop"
                         :label="column.label"
                         :width="120"
        >
          <template slot-scope="scope">
            <el-button v-if="column.label === 'Name'" @click.native="callId(scope.row.id)">{{ scope.row.name }}</el-button>
            <span v-else>{{ scope.row[column.prop] }}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  components: {},
  data() {
    return {
      search: '',
      columns: [
        {
          prop: 'name',
          label: 'Name',
        },
        {
          prop: 'email',
          label: 'Email',
        },
        {
          prop: 'gezahlt/preis',
          label: 'Gezahlt / Preis',
        },
        {
          prop: 'checkin/checkout',
          label: 'Check-In/ Check-Out',
        },
      ],
      tableData: []
    };
  },
  mounted() {
    this.getData()
  },
  methods: {
    getData() {
      axios
          .get('/all')
          .then((response) => {
            const unprocessed = JSON.parse(JSON.stringify(response.data))
            console.log(unprocessed)
            let rows = []
            for (let j = 0; j < unprocessed.length; j++) {
              const obj = {
                'name': unprocessed[j]['name'],
                'email': unprocessed[j]['email'],
                'id': unprocessed[j]['id'],
                'gezahlt/preis': unprocessed[j]['gezahlt'] + '/' + unprocessed[j]['preis'],
                'checkin/checkout': unprocessed[j]['checkIn'] + '/' + unprocessed[j]['checkOut'],
              }
              rows.push(obj)
            }
            this.tableData = rows
          })
    },
    doSearch() {
      this.search= ''
      console.log("hiiiiii")
    },
    callId(id) {
      console.log("id: " + id)
    }
  }
}
</script>

<style>
.el-dropdown-link {
  cursor: pointer;
  color: #409EFF;
}

.el-icon-arrow-down {
  font-size: 12px;
}

.el-tooltip__popper {
  max-width: 500px;
}
</style>
